* {
  margin: 0px;
  padding: 0px;
}
html {
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  width: 100%;
  height: 100vh;
  color: #121110;
  font-family: "NotoSansKR", "Poppins", sans-serif !important;
}
::selection {
  background: #b4fcbe;
}
a {
  text-decoration: none;
}
