.sideMenu {
  position: fixed;
  background-color: #ffffff;
  width: 20%;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 999;
  transform: translateX(-100%);
  transition: 0.5s;
  padding: 10px;
  border-right: 1px solid #000;
}
.wholeScreen {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 998;
}

.sideMenuActive {
  transform: translateX(0);
}
.sideMenuClose {
  position: absolute;
  top: 3%;
  right: 5%;
  width: 15px;
  cursor: pointer;
  z-index: 888;
}
.sideMenuTop {
  position: absolute;
  top: 10%;
  display: flex;
  width: calc(100% - 20px);
  justify-content: space-between;
}
.login {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.login_span {
  font-size: 11px;
  padding-left: 8px;
}
.sideMenu_detail {
  display: flex;
  align-items: center;
}
.language {
  font-size: 11px;
  cursor: pointer;
}
.search {
  padding-left: 10px;
  cursor: pointer;
}
.cart {
  padding-left: 10px;
  padding-right: 7px;
  cursor: pointer;
}
.sideMenuMiddle {
  position: absolute;
  top: 23%;
  display: block;
  width: calc(100% - 20px);
  font-size: 18px;
  font-weight: 700;
}

.menuList {
  padding: 7px 0;
  text-align: center;
  cursor: pointer;
  transform: translateX(0%);
  transition: 0.8s;
}
.sideMenuBack {
  position: absolute;
  left: -25px;
  transition: 0.3s;
  transform: translateX(-2000%);
}
.sideMenuBackActive {
  transform: translateX(0%);
}
.menuListActive {
  transform: translateX(-1000%);
}
.women_menuOn {
  transform: translateX(0%);
}

