/* --------------------------------------------------------- */
.homeImg {
  width: 100%;
  height: 100vh;
}
.homeImg img {
  width: 100%;
}
.logo {
  position: fixed;
  top: 38%;
  width: 15%;
  left: 4%;
  z-index: 9999;
  transition: 0.5s;
  cursor: pointer;
}
.logoActive {
  left: 30%;
}
.logo img {
  border: none;
  vertical-align: top;
}
.bottomLogo {
  position: fixed;
  bottom: 4%;
  width: 2%;
  left: 50%;
  z-index: 9999;
  transform: translate(-50%, 0);
  cursor: pointer;
  transition: 0.5s;
}
.bottomLogoActive {
  left: 62%;
  transform: translate(-62%, 0);
  bottom: 7%;
}
.bottomLogo img {
  border: none;
  vertical-align: top;
}
.menu {
  position: fixed;
  bottom: 3%;
  width: 8%;
  left: 0%;
  z-index: 9999;
  font-size: 20px;
  cursor: pointer;
  height: 4%;
}
.menu span {
  font-size: 0;
  width: 40px;
  height: 5px;
  display: block;
  background: black;
  border-radius: 15px;
  text-align: center;
  margin: 7px auto;
  box-shadow: 1px 1px 2px 0px;
  color: #fff;
}
